export default {
	data() {
		return {
			showActive:false,
			clubId:'',
			list: [],
		    isFinish: false,
		    condition_list: ['提交中', '已取消', '审核中', '已拒绝', '已通过', '全部'],
		    show_menu: false,
		    default_list: [],
		    cate: '筛选',
			status:'',
			list2:[]
		}
	},
	onShow() {
		this.getData()
		this.cate = '筛选'
	},
	onLoad(e) {
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});
		this.clubId = e.clubId
		
	},
	methods: {
		apply(){
			uni.navigateTo({
				url:'../../pagesThree/FundsApply/FundsApply?clubId='+ this.clubId
			})
		},
		// 详情
		goContent(item){
			uni.navigateTo({
				url:'../../pagesThree/FundsApplyDetail/FundsApplyDetail?id='+ item.id
			})
		},
		tab(item,ind){
			this.cate = item
			this.showActive = !this.showActive
			if(ind == 5){
				this.list = this.list2
			}else{
				// console.log(ind)
				this.list = this.list2.filter((item,index) => item.status== ind)
			}
			
		},
		getData(){
			this.$api.fund_list({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				club_id:this.clubId
			}).then(res=>{
				uni.hideLoading();
				if(res.data.code=="200"){
					this.list = res.data.data
					this.list2 = res.data.data
					this.isFinish = true
				}else{
					uni.showToast({
					    title: res.data.msg,
					    mask: "true",
						icon:'none'
					});
				}
				
			})
		}
	}
}
